body {
  font-family: "Roboto", Helvetica, sans-serif;
  font-weight: 400;
  background: #333;
  color: #fff;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 600;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

a {
  color: #fff;
  display: inline-block;
}

a:hover, a:focus {
  text-decoration: none;
}

button,
[type='button'] {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  border: 1px solid #fff;
  border-radius: 3px;
  padding: 5px 10px;
  color: #fff;
  background: none;
  -webkit-box-shadow: none;
          box-shadow: none;
  cursor: pointer;
}

button:hover, button:focus,
[type='button']:hover,
[type='button']:focus {
  background: #fff;
  color: #333;
}

button + button,
button + [type='button'],
[type='button'] + button,
[type='button'] + [type='button'] {
  margin-left: 15px;
}

img {
  max-width: 100%;
  height: auto;
  width: auto;
}

form input,
form label {
  clear: both;
  display: block;
}

form input {
  margin-bottom: 1.5em;
}

.loader {
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: 10;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  top: 0;
  left: 0;
  background: black;
  opacity: 1;
  -webkit-transition: all 0.25s;
  transition: all 0.25s;
}

.loader.hidden {
  z-index: -1;
  opacity: 0;
  visibility: hidden;
}

.container {
  max-width: 994px;
  margin: 0 auto;
}

@media (max-width: 1023px) {
  .container {
    padding: 0 15px;
  }
}

.link__back {
  display: block;
  clear: both;
  margin-top: 25px;
  margin-left: 15px;
  float: left;
}

.profiles__view-switch {
  clear: both;
  width: 100%;
  overflow: hidden;
  margin-bottom: 10px;
}

.view-switch__icon {
  float: right;
  margin-left: 10px;
}

.view-switch__icon svg {
  display: block;
  width: 32px;
  height: 32px;
  fill: #fff;
}

.profiles__list {
  border-radius: 6px;
}

.profiles__list.profiles__list--grid {
  margin-left: -15px;
  margin-right: -15px;
}

.profiles-list__item {
  border: 1px solid #fff;
  border-bottom-width: 0;
  position: relative;
  cursor: pointer;
}

.profiles-list__item:before {
  content: '';
  position: absolute;
  display: block;
  top: 0;
  left: 0;
  height: 100%;
  width: 0;
  z-index: -1;
  -webkit-transition: width 0.5s ease;
  transition: width 0.5s ease;
  background: #666;
  -webkit-transition-timing-function: cubic-bezier(0.52, 1.64, 0.37, 0.66);
          transition-timing-function: cubic-bezier(0.52, 1.64, 0.37, 0.66);
}

.profiles-list__item:hover:before, .profiles-list__item:focus:before {
  width: 100%;
}

.profiles__list--grid .profiles-list__item {
  -webkit-transition: -webkit-transform 0.25s ease;
  transition: -webkit-transform 0.25s ease;
  transition: transform 0.25s ease;
  transition: transform 0.25s ease, -webkit-transform 0.25s ease;
}

.profiles__list--grid .profiles-list__item:hover, .profiles__list--grid .profiles-list__item:focus {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

.profiles__list--grid .profiles-list__item:before {
  content: none;
}

@media (max-width: 319px) {
  .profiles-list__item {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    padding: 10px;
  }
}

@media (min-width: 320px) {
  .profiles-list__item {
    padding: 20px;
  }
}

.profiles-list__item:last-of-type {
  border-bottom-width: 1px;
}

.profiles__list--grid .profiles-list__item {
  border-bottom-width: 1px;
  margin-bottom: 15px;
  margin-right: 15px;
  margin-left: 15px;
  border-radius: 6px;
}

@media (min-width: 768px) {
  .profiles__list--grid .profiles-list__item {
    display: inline-block;
    float: left;
    margin-bottom: 30px;
  }
}

@media (min-width: 768px) and (max-width: 1023px) {
  .profiles__list--grid .profiles-list__item {
    width: calc(50% - 30px);
  }
}

@media (min-width: 1024px) {
  .profiles__list--grid .profiles-list__item {
    width: calc(100%/3 - 30px);
  }
}

.profiles-list__preview {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

@media (max-width: 319px) {
  .profiles-list__name {
    margin-bottom: 15px;
  }
}

@media (min-width: 768px) {
  .profile {
    margin-left: -15px;
    margin-right: -15px;
  }
}

@media (max-width: 767px) {
  .profile__remove-wrapper {
    overflow: hidden;
  }
}

.profile__remove-btn {
  float: right;
}

.profile__image,
.profile__info {
  width: 100%;
}

@media (min-width: 768px) {
  .profile__image,
  .profile__info {
    margin-right: 15px;
    margin-left: 15px;
    float: left;
    width: calc(50% - 30px);
  }
}

@media (max-width: 767px) {
  .profile__image {
    margin-bottom: 25px;
  }
}

.profile__img {
  display: block;
  margin: 0 auto;
}

.profile__attr-title {
  font-weight: 600;
  font-size: 1.25em;
  margin: 0;
}

.profile__attr {
  margin-top: 0.5em;
}

.modal {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.3);
  opacity: 0;
  visibility: hidden;
  -webkit-transition: visibility ease 0.25s, opacity ease 0.25s;
  transition: visibility ease 0.25s, opacity ease 0.25s;
}

.modal.open {
  opacity: 1;
  visibility: visible;
}

.modal.hide-all:before {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  display: block;
  content: '';
  background: #333;
  z-index: -1;
}

.modal button {
  background: #333;
  color: #fff;
  border-color: #333;
}

.modal a {
  color: #333;
}

.modal-wrapper {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate3D(-50%, -50%, 1px);
          transform: translate3D(-50%, -50%, 1px);
  background-color: #fff;
  color: #333;
  padding: 20px;
  width: 497px;
  max-width: 100%;
  border-radius: 6px;
}

.hidden {
  display: none;
}
